import React, {memo} from "react";
import createMarkup from './createMarkup';
function Step(props) {
    let { data, stepIndex } = props;

    return (
        <div
            className={`step__outer step__outer__${stepIndex}`}
            data-step={stepIndex}
            key={`step__outer__${stepIndex}`}
        >
            <div
                className={`step__inner step__inner__${stepIndex}`}
                key={`step__inner__${stepIndex}`}
            >
                { data['date'] && <h3
                    key={`step__inner__hed__${stepIndex}`}
                    className={`step__inner__hed`}
                >
                    {data["date"]}
                </h3>}
                <p
                    key={`step__inner__p__${stepIndex}`}
                    dangerouslySetInnerHTML={createMarkup(data["text"])}
                ></p>
            </div>
        </div>
    );
}

export default memo(Step);
